import React from 'react';
import { Table } from 'react-bootstrap';
import Post from '../Post/Post';
import { Post as PostInterface, Posts } from '../../service/postsRepository';
import './board.css';
import Spinner from './../Spinner/Spinner';

interface BoardProps {
  setAllHashtags: React.Dispatch<any>;
  posts: Posts;
  onDeletePost: (post: PostInterface) => void;
  userId: string;
  filteredPosts: any;
  isLoading: boolean;
}

const Board: React.FC<BoardProps> = ({
  posts,
  onDeletePost,
  userId,
  filteredPosts,
  isLoading,
}) => {
  return (
    <>
      <Table bordered className='board-table'>
        <thead>
          <tr>
            <th className='date-th'></th>
            <th className='city-th'></th>
            <th className='temp-th'></th>
            <th className='weather-th'></th>
            <th className='clothes-th'></th>
            <th className='del-th'></th>
          </tr>
        </thead>
        <tbody>
          {filteredPosts
            ? Object.keys(filteredPosts)
                .sort((a, b) => {
                  return (
                    new Date(posts[b]['date']).valueOf() -
                    new Date(posts[a]['date']).valueOf()
                  );
                })
                .map((key: any) => (
                  <Post
                    key={key}
                    post={posts[key]}
                    onDeletePost={onDeletePost}
                    userId={userId}
                  />
                ))
            : Object.keys(posts)
                .sort((a, b) => {
                  return (
                    new Date(posts[b]['date']).valueOf() -
                    new Date(posts[a]['date']).valueOf()
                  );
                })
                .map((key: any) => (
                  <Post
                    key={key}
                    post={posts[key]}
                    onDeletePost={onDeletePost}
                    userId={userId}
                  />
                ))}
        </tbody>
      </Table>
      {isLoading && <Spinner />}
    </>
  );
};

export default Board;
