import React, { useState } from 'react';
import { Form } from 'react-bootstrap';
import { Posts } from '../../service/postsRepository';
import './filter.css';

interface FilterProps {
  dates: string[] | [];
  setFilteredPosts: React.Dispatch<React.SetStateAction<Posts | false>>;
  posts: Posts;
}

interface FilterState {
  date: string;
  city: string;
  temp: string;
}

const Filter: React.FC<FilterProps> = ({ dates, setFilteredPosts, posts }) => {
  const [filter, setFilter] = useState({ date: '', city: '', temp: '' });

  const onChangeFilter = (e: { target: { value: any; id: any } }) => {
    switch (e.target.id) {
      case 'dateFilter':
        setFilter((cur: FilterState) => ({ ...cur, date: e.target.value }));
        break;
      case 'cityFilter':
        setFilter((cur: FilterState) => ({ ...cur, city: e.target.value }));
        break;
      case 'tempFilter':
        setFilter((cur: FilterState) => ({
          ...cur,
          temp: e.target.value !== '' ? e.target.value.split('~') : '',
        }));
        break;
      default:
        return;
    }
  };

  const onFilter = () => {
    if (filter.city === '' && filter.date === '' && filter.temp === '') {
      setFilteredPosts(false);
    } else {
      setFilteredPosts(() => {
        const updated = { ...posts };
        Object.keys(updated).forEach((key: any) => {
          if (
            !updated[key]['date'].includes(filter.date) ||
            !updated[key]['city'].includes(filter.city) ||
            !(
              updated[key]['city'].includes(filter.temp) ||
              (updated[key]['temp'] > parseInt(filter.temp[0]) &&
                updated[key]['temp'] <= parseInt(filter.temp[1]))
            )
          ) {
            delete updated[key];
          }
        });
        return updated;
      });
    }
  };

  return (
    <div className='d-flex mb-3 filter'>
      <div className='me-3'>
        <Form.Select onChange={onChangeFilter} id='dateFilter'>
          <option value=''></option>
          {dates
            .sort((a, b) => {
              return new Date(b).valueOf() - new Date(a).valueOf();
            })
            .map((date, index) => (
              <option key={index} value={date}>
                {date}
              </option>
            ))}
        </Form.Select>
      </div>
      <div className='me-3'>
        <Form.Select onChange={onChangeFilter} id='cityFilter'>
          <option value=''></option>
          <option value=''></option>
          <option value=''></option>
          <option value=''></option>
          <option value=''></option>
          <option value=''></option>
          <option value=''></option>
          <option value=''></option>
          <option value=''></option>
          <option value=''></option>
          <option value=''></option>
          <option value=''></option>
          <option value=''></option>
          <option value=''></option>
          <option value=''></option>
          <option value=''></option>
          <option value=''></option>
          <option value=''></option>
        </Form.Select>
      </div>
      <div className='me-3'>
        <Form.Select onChange={onChangeFilter} id='tempFilter'>
          <option value=''>()</option>
          <option value='25~30'>25~30</option>
          <option value='20~25'>20~25</option>
          <option value='15~20'>15~20</option>
          <option value='10~15'>10~15</option>
          <option value='5~10'>5~10</option>
          <option value='0~5'>0~5</option>
          <option value='-5~0'>-5~0</option>
          <option value='-10~-5'>-10~-5</option>
        </Form.Select>
      </div>
      <button onClick={onFilter} className='btn btn-outline-primary'>
        
      </button>
    </div>
  );
};

export default Filter;
