const useClothes = (temp?: number) => {
  if (temp) {
    switch (true) {
      case temp >= 28:
        return ', , ,  ';
      case temp >= 23:
        return ',  , , ';
      case temp >= 20:
        return ' , , , ';
      case temp >= 17:
        return ' , , ,  , , ';
      case temp >= 12:
        return ', , , , , , , ';
      case temp >= 9:
        return ', , , , , , ';
      case temp >= 5:
        return ', /, , , ';
      case temp < 5:
        return ',  , , , /';
      default:
        return '  .';
    }
  }
  return ', , ,  , , , , , , , , , , , , , /, , , '.split(
    ', '
  );
};

export default useClothes;
